.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
/* ===================================================================== */
/* Top title */
.black-nav {
  background : black;
  width : 100%;
  display : flex;
  color : white;
  padding : 30px;
  font-weight : 600;
  font-size : 20px;
}

/* 각 글제목 리스트 */
.list {
  margin-top : 30px;
  text-align : left;
  padding-left : 20px;
  padding-right : 20px;
}

/* 모달창 */
.modal{
  margin-top : 20px;
  padding : 20px;
  background : #eee;
}

/* 글작성 UI <div> */
.publish {
  margin-top : 30px;
  margin-bottom : 30px;
}

/* 글작성 UI <input> */
.publish input {
  padding : 10px;
  font-size : 16px;
  border-radius : 5px;
  width : 80%;
  border : 1px solid grey;
}

/* 글작성 UI <button> */
.publish button {
  display : block;
  margin : auto;
  margin-top : 10px;
}






